.resultado{

    .score{
        h2{
            padding-bottom: 3rem; 
            text-align: center;
            span{
                color: black;
            }
        }
        .cont-r{
            text-align: center;
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 2rem;
            margin-bottom: 6rem;
        }
        h4{
            border: 1px solid lightgray;
            border-radius: 10px;
            padding: 1rem 2rem;
            margin: 0;
            font-size: 1.8rem;
            font-weight: normal;
            &.r-correct{
                border: 1px solid green;
                background-color: lightgreen;
                color: darkgreen;
            }
            &.r-incorrect{
                border: 1px solid red;
                background-color: rgb(248, 123, 123);
                color: rgb(140, 0, 0);
            }
        }
    }
    a{
        text-decoration: none;
        border: 1px solid gray;
        border-radius: 10px;
        background-color: lightgray;
        color: black;
        font-size: 1.4rem;
        padding: 1rem 2rem;
        margin-top: 3rem;
        &:hover{
            font-weight: bold;
            border: 1px solid darkgray;
            cursor: pointer;
        }
    }
    .r-score{
        text-align: center;
        font-weight: bold;
        margin-top: 5rem;
        h3{margin-top: 1rem;}
    }
    .puntaje{
        margin-top: 2rem;
        text-align: center;
    }
}