.main-banner {
    max-width: 100%;
    margin: 0 20%;
    height: calc(90vh - 8rem);
    //width: 0%;

    display: grid;
    place-items: center;

    .contenedor-banner {
        max-width: fit-content;
        min-width: -webkit-fill-available;
        display: block;
        border: 0.5rem solid lightgray;
        padding: 1rem;
    }

    .titulo-banner {
        margin: 0 0 4rem;
        font-size: 17rem;
        border-bottom: 2px solid gray;
        color: #444;
        text-align: center;
    }

    .frase-banner {
        padding: 0 2rem;
        font-size: 2.5rem;
        color: gray;
    }

    .autor-banner {
        padding: 0 2rem;
        font-size: 2.5rem;
        color: gray;
        padding-top: 1rem;
        text-align: right;

    }
}