body {
    font-family: 'PT Serif'
}

html {
    font-size: 62.5%;
    box-sizing: border-box;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

.contenedor {
    max-width: 90%;
    margin: 0 auto;
}

h1 {
    font-size: 4.8rem;
}

h2 {
    font-size: 4rem;
}

h3 {
    font-size: 3.2rem;
}

h4 {
    font-size: 2.4rem;
}

p {
    font-size: 1.4rem;
    margin: 0;
    line-height: 1.5;
}