        .opc-cont{
            text-align: center;
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 2rem;
            label{
                margin: 0;
                display: block;
                font-size: 1.8rem;
                border: 1px solid lightgray;
                border-radius: 10px;
                padding: 1rem 2rem;
                &:hover{
                    font-weight: bold;
                    cursor: pointer;
                    border: 1px solid black;
                }
                &.selected{
                    background-color: lightgrey;
                    font-weight: bold;
                    border: 1px solid black;
                }
            }
        }
        .timer{
            height: 4rem;
            .time{
                font-size: 3rem;
                font-weight: bold;
                margin-top: 2rem;
                text-align: right;
            }
        }
    }
    .revisar{
        border: 1px solid darkgray;
        margin-top: 2rem;
        font-size: 1.4rem;
        padding: 1rem 2rem;
        width: fit-content;
        &:hover{
            cursor: pointer;
            font-weight: bold;
        }
}