main{
    margin-top: 5rem;
}
.edit-btn{
    padding-top: 5rem;
    text-decoration: none;
    padding: 1rem 2rem;
    background-color: black;
    color: white;
    font-size: 1.4rem;
    &:hover{
        cursor: pointer;
        font-weight: bold;
    }
}
.puntajes{
    margin-top: 5rem;
    justify-content: center;
    text-align: center;
    table{
        border-spacing: 0;
        font-size: 1.8rem;
    }
    thead{
        background-color: black;
        color: white;
        th{
            padding-left: 5rem;
            padding-right: 5rem;
            padding-bottom: 1rem;
            padding-top: 1rem;
        }
    }
    tbody{
        td{
            padding-left: 5rem;
            padding-right: 5rem;
            padding-bottom: 2rem;
            padding-top: 2rem;
            border-bottom: 1px solid darkgray;
            display: flex;
            align-items: center;
            &:last-of-type{
                padding-bottom: 0;
            }
        }
    }

}
.edit-form{
    font-size: 1.2em;
    margin-top: 5rem;
    label{font-weight: bold;
    margin-bottom: 1rem;}
    .option{
        flex-direction: row;
        align-items: center;
        gap: 2rem;
        margin-top: 1rem;
        margin-bottom: 0;
        label{font-weight: bold;}
    }
    div{
        margin-bottom: 2rem;
        display: flex;
        flex-direction: column;
    }
        select{
            width:max-content;
        }
}
.edit{
    h3{
        text-align: center;
    }
    padding-bottom: 5rem;
}
.hidden{
    display: none;
}
.update{
    text-align: center;
}