.header {
    padding: 2rem 0 2rem 0;
    border-bottom: 3px solid black;
    box-shadow: 0 1px gray;
    background-color: green;

    .cont-header {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .img-header {
            height: 8rem;
        }

        .user-info {
            display: flex;
            align-items: center;
            gap: 2rem;

            p {
                font-weight: bold;
                font-size: 2.7rem;
            }

            .img-user {
                height: 6.5rem;
                border-radius: 50%;
            }
        }
    }
}


