.main-login {
    max-width: 100%;
    margin: 0 15%;
    height: 95vh;

    display: grid;
    place-items: center;

    .contenedor-login {
        min-width: 90%;
        width: 50%;

        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 1px;

        .bloque {
            display: flex;
            justify-content: center;
            align-items: center;

            img {
                height: 65vh;
                border-radius: 10px;
                border: 2px solid black;
            }

            form {
                display: flex;
                flex-direction: column;
                align-items: center;
                width: 90%;

                h3 {
                    font-size: 3.5vw;
                    margin: 0.5rem;
                    text-align: left;
                }

                .fieldset {
                    margin-top: 5rem;

                    .field {
                        display: flex;
                        flex-direction: column;
                        font-size: 1.4rem;
                        margin-bottom: 3.5rem;

                        label {
                            font-size: 1.6rem;
                            font-weight: 500;
                        }

                        input {
                            width: 20vw;
                            font-size: 2.2rem;
                            border-radius: 5px;
                            border: 1px solid gray;
                            padding: 0.5rem;
                        }
                    }
                }

                .btn-enviar {
                    margin-top: 3rem;
                    font-size: 2rem;
                    font-weight: bold;
                    background-color: lightgrey;
                    border: 1px solid gray;
                    border-radius: 1rem;
                    padding: 0.7rem 2rem;
                    margin-bottom: 12.5vh;
                }
            }
        }
    }
}